import {store} from '../store';
import {REQUEST_SDK} from './sdk';


export const chats = {
    async getChats(limit = 100, skip = 0) {
        const res = await REQUEST_SDK.GetChats({
            limit,
            skip
        });

        return res.chatting?.chats || [];
    },

    async getMessages(chat: string, limit = 100, skip = 0) {
        const res = await REQUEST_SDK.GetMessages({
            chat,
            limit,
            skip,
        });

        return res.chatting?.messages || [];
    },

    async sendMessage(chat: string, text: string) {
        const res = await REQUEST_SDK.SendMessage({
            chat,
            text,
        });

        return res.mutationChat!.sendMassage!;
    },
} as const;

